import { Injectable } from "@angular/core";
import { Http, Headers, RequestOptions, Response } from "@angular/http";
import { Subject } from "rxjs/Subject";

@Injectable()
export class ClickService{

    private archiveClick = new Subject<boolean>();
    private archiveBarStatus: boolean = false;
    archiveClick$ = this.archiveClick.asObservable();

    private bodyClick = new Subject<boolean>();
    private bodyMoveStatus: boolean = false;
    bodyClick$ = this.bodyClick.asObservable();

    private zoomStatus = new Subject<number>();
    private zoomStatusNumber: number = 1;
    zoomStatus$ = this.zoomStatus.asObservable();

    private undoClick = new Subject<boolean>();
    undoClick$ = this.undoClick.asObservable();

    private saveFile = new Subject<boolean>();
    private saveFileStatus: boolean = false;
    saveFile$ = this.saveFile.asObservable();

    private archiveConfirmation = new Subject<boolean>();
    archiveConfirmation$ = this.archiveConfirmation.asObservable();

    private sideMenu = new Subject<string>();
    private sideMenuStatus: string = 'in';
    sideMenu$ = this.sideMenu.asObservable();

    private alertBox = new Subject<boolean>();
    private alertBoxStatus: boolean = false;
    alertBox$ = this.alertBox.asObservable();

    private zoomLevel = new Subject<number>();
    private zoomInt: number = 0.8;
    zoomLevel$ = this.zoomLevel.asObservable();

    zoomIn(){
        console.log(this.zoomInt);
        this.zoomInt += 0.1;
        this.zoomLevel.next(this.zoomInt);
    }
    zoomOut(){
        console.log(this.zoomInt)
        this.zoomInt -= 0.1;
        this.zoomLevel.next(this.zoomInt);
    }
    openArchive(){
        this.archiveClick.next(this.archiveBarStatus = !this.archiveBarStatus);
    }
    closeArchive(){
        this.archiveClick.next(false);
    }
    moveBody(){
        this.bodyClick.next(this.bodyMoveStatus = !this.bodyMoveStatus);
    }
    closeBody(){
        this.bodyClick.next(false);
    }
    undoLast(){
        this.undoClick.next(true);
    }
    showSaveFileModal(){
        this.saveFile.next(true)
    }
    closeSaveFileModal(){
        this.saveFile.next(false);
    }
    openSideMenu(){
        this.sideMenu.next('out');
    }
    closeSideMenu(){
        this.sideMenu.next('in');
    }
    showAlertBox(){
        this.alertBox.next(true);
    }
    hideAlertBox(){
        this.alertBox.next(false);
    }
    openConfirmation(){
        this.archiveConfirmation.next(true);
    }
    closeConfirmation(){
        this.archiveConfirmation.next(false);
    }
    /*zoomIn(zi: number){
        this.zoomStatusNumber = this.zoomStatusNumber + zi;
        if(zi > 1.5){
            zi = 1.5;
            this.zoomStatusNumber = zi;
        }
        this.zoomStatus.next(this.zoomStatusNumber);

    }
    zoomOut(zo: number){
        if(zo < 0.5){
            zo = 0.5;
            this.zoomStatusNumber = zo;

        }
        this.zoomStatus.next(this.zoomStatusNumber);
    }*/


}
